const baseUrl = 'https://jc578271.github.io/assets/'
// const baseUrl = 'http://127.0.0.1:5001/masterproject-4bad2/asia-southeast1/server/'
const appName = 'policy'

const require = (path: string) => {
  const arr = path.substring(1).split('.')
  const name = arr[0] + '@3x'
  const ext = arr[1]

  // console.log(baseUrl + appName + name + '.' + ext);

  return {
    uri: baseUrl + appName + name + '.' + ext
  }
}

